import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { string } from '@amcharts/amcharts4/core';


@Component({
  selector: 'kt-create-work-center',
  templateUrl: './create-work-center.component.html',
  styleUrls: ['./create-work-center.component.scss']
})
export class CreateWorkCenterComponent implements OnInit {

workcenter : string;
machine: string;
description: string;
depart: string;

wcForm: FormGroup;
hasFormErrors: boolean = false;
constructor(
  private activatedRoute: ActivatedRoute,
  private router: Router,
  private wcFB: FormBuilder
) { }



ngOnInit() {
  this.initwcForm();
}

initwcForm() {

  this.wcForm = this.wcFB.group({
    workcenter: [this.workcenter, Validators.required],
    machine: [this.machine, Validators.required],
    description: [this.description, Validators.required],
    depart: [this.depart, Validators.required],
   
  })
}




 

}
