import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { string } from '@amcharts/amcharts4/core';


@Component({
  selector: 'kt-create-nomenclature',
  templateUrl: './create-nomenclature.component.html',
  styleUrls: ['./create-nomenclature.component.scss']
})
export class CreateNomenclatureComponent implements OnInit {

  code: string;
  description: string;
  compose: string;
  compodesc: string; 
  composant: string;

  nmForm: FormGroup;
  prodForm: FormGroup;
  compForm: FormGroup;
  hasFormErrors: boolean = false;
  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private nmFB: FormBuilder,
    private prodFB: FormBuilder,
    private compFB: FormBuilder
  ) { }



  ngOnInit() {
    this.initnmForm();
    this.initprodForm();
    this.initcompForm();
  }

  initnmForm() {
  
    this.nmForm = this.nmFB.group({
      code: [this.code, Validators.required],
      description: [this.description, Validators.required],
    })
  }
  initprodForm() {
  
    this.prodForm = this.prodFB.group({
      compose: [this.compose, Validators.required],
      compodesc: [this.compodesc, Validators.required],
    })
  }
  initcompForm() {
    this.compForm = this.compFB.group({
    composant: [this.composant, Validators.required],
     
    })
  }

}
