import { Component, OnInit } from "@angular/core"
import { FormBuilder, Validators, FormGroup } from "@angular/forms"
import { ActivatedRoute, Router } from "@angular/router"
import { string } from "@amcharts/amcharts4/core"

@Component({
    selector: "kt-create-order",
    templateUrl: "./create-order.component.html",
    styleUrls: ["./create-order.component.scss"],
})
export class CreateOrderComponent implements OnInit {
    nof: string
    id: string
    product: string
    desc1: string
    desc2: string
    type: string
    site: string
    status: string
    site2: string
    liaison: string
    gamme: string
    csata: string
    scsata: string
    ccsata: string
    ceum: string
    sceum: string
    cceum: string
    cetm: string
    scetm: string
    ccetm: string
    ceme: string
    sceme: string
    cceme: string
    cprod: string
    scprod: string
    ccprod: string
    ceust: string
    sceust: string
    cceust: string
    ctst: string
    sctst: string
    cctst: string
    cencour: string
    scencour: string
    ccencour: string
    cecof: string
    scecof: string
    ccecof: string
    value: Boolean = false

    ofForm: FormGroup
    hasFormErrors: boolean = false
    constructor(
        private activatedRoute: ActivatedRoute,
        private router: Router,
        private ofFB: FormBuilder
    ) {}
    onChange($event) {
      $event.target.checked ? this.value = true : this.value = false
    }

    ngOnInit() {
        this.initofForm()
    }

    initofForm() {
        this.ofForm = this.ofFB.group({
            nof: [this.nof, Validators.required],
            id: [this.id, Validators.required],
            product: [this.product, Validators.required],
            site: [this.site, Validators.required],
            site2: [this.site2, Validators.required],
            gamme: [this.gamme, Validators.required],
            csata: [this.csata, Validators.required],
            ceum: [this.ceum, Validators.required],
            cetm: [this.cetm, Validators.required],
            ceme: [this.ceme, Validators.required],
            cprod: [this.cprod, Validators.required],
            ceust: [this.ceust, Validators.required],
            ctst: [this.ctst, Validators.required],
            cencour: [this.cencour, Validators.required],
            cecof: [this.cecof, Validators.required],
        })
    }
}
