import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { string } from '@amcharts/amcharts4/core';
@Component({
  selector: 'kt-create-gamme',
  templateUrl: './create-gamme.component.html',
  styleUrls: ['./create-gamme.component.scss']
})
export class CreateGammeComponent implements OnInit {

  code: string;
  workcenter: string;
  machine: string;
  description: string;

  
  gammeForm: FormGroup;
  hasFormErrors: boolean = false;
  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private gammeFB: FormBuilder
  ) { }
  
  
  
  ngOnInit() {
    this.initgammeForm();
  }
  
  initgammeForm() {
  
    this.gammeForm = this.gammeFB.group({
      code: [this.code, Validators.required],
      workcenter: [this.workcenter, Validators.required],
      machine: [this.machine, Validators.required],
      description: [this.description, Validators.required],
    })
  }
  
  
  
  
}
